import * as R from 'ramda'
import {FETCH_PHONES_SUCCESS, LOAD_MORE_SUCCESS, FETCH_BY_ID_SUCCESS} from 'actionTypes'

const initialState = {}

export default (state = initialState, {type, payload}) => {
    switch (type) {
        case FETCH_PHONES_SUCCESS:
            const newValues = R.indexBy(R.prop('id'), payload)
            return R.merge(state, newValues)
        case LOAD_MORE_SUCCESS:
            const moreValues = R.indexBy(R.prop('id'), payload)
            return R.merge(state, moreValues)
        case FETCH_BY_ID_SUCCESS:
            return R.assoc(payload.id, payload, state)
            /*phone id needed to be as a key, assoc makes a shallow clone of an object. prototype properties copies and flattens onto the new object.*/
        default :
            return state
    }
}